'use client';

import { useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';

interface BlogPost {
  id: string;
  title: string;
  excerpt: string;
  content: string;
  author: string;
  authorAvatar: string;
  publishedAt: string;
  readTime: string;
  category: string;
  tags: string[];
  image: string;
  featured: boolean;
  views: number;
  likes: number;
}

const blogPosts: BlogPost[] = [
  {
    id: 'traditional-pickle-making',
    title: 'The Art of Traditional South Indian Pickle Making',
    excerpt: 'Discover the ancient secrets behind authentic South Indian pickles. From selecting the right mangoes to the perfect oil blend, learn the traditional techniques passed down through generations.',
    content: 'In-depth article about traditional pickle making...',
    author: 'Priya Sharma',
    authorAvatar: '/avatars/author1.jpg',
    publishedAt: '2024-09-10',
    readTime: '8 min read',
    category: 'Traditional Recipes',
    tags: ['pickles', 'traditional', 'south indian', 'recipes'],
    image: '/categories/mango-pickle.jpg',
    featured: true,
    views: 2847,
    likes: 234
  },
  {
    id: 'fermentation-science',
    title: 'The Science Behind Perfect Idli Batter Fermentation',
    excerpt: 'Understanding the microbiology of fermentation helps create the perfect idli batter. Learn about the role of wild yeasts, lactobacilli, and environmental factors.',
    content: 'Detailed scientific explanation of fermentation...',
    author: 'Dr. Rajesh Kumar',
    authorAvatar: '/avatars/author2.jpg',
    publishedAt: '2024-09-05',
    readTime: '12 min read',
    category: 'Food Science',
    tags: ['fermentation', 'idli', 'science', 'batter'],
    image: '/categories/Batters.jpg',
    featured: true,
    views: 1923,
    likes: 189
  },
  {
    id: 'spice-health-benefits',
    title: 'Health Benefits of Traditional South Indian Spices',
    excerpt: 'Explore the medicinal properties of turmeric, coriander, fenugreek, and other spices commonly used in South Indian cuisine. Ancient wisdom meets modern nutrition science.',
    content: 'Comprehensive guide to spice health benefits...',
    author: 'Dr. Meera Nair',
    authorAvatar: '/avatars/author3.jpg',
    publishedAt: '2024-08-28',
    readTime: '10 min read',
    category: 'Health & Nutrition',
    tags: ['spices', 'health', 'nutrition', 'ayurveda'],
    image: '/categories/powders.jpg',
    featured: false,
    views: 3156,
    likes: 412
  },
  {
    id: 'regional-variations',
    title: 'Regional Variations in South Indian Cuisine',
    excerpt: 'Journey through Tamil Nadu, Karnataka, Kerala, and Andhra Pradesh to discover how geography and culture shape unique culinary traditions across South India.',
    content: 'Exploration of regional food differences...',
    author: 'Anita Reddy',
    authorAvatar: '/avatars/author4.jpg',
    publishedAt: '2024-08-20',
    readTime: '15 min read',
    category: 'Culture & Heritage',
    tags: ['regional', 'culture', 'heritage', 'south india'],
    image: '/categories/veg.jpg',
    featured: true,
    views: 2234,
    likes: 298
  },
  {
    id: 'sustainable-farming',
    title: 'Sustainable Farming Practices for Organic Spices',
    excerpt: 'Learn how traditional farming methods combined with modern sustainable practices ensure the highest quality organic spices while protecting the environment.',
    content: 'Article about sustainable farming...',
    author: 'Krishnan Iyer',
    authorAvatar: '/avatars/author5.jpg',
    publishedAt: '2024-08-15',
    readTime: '7 min read',
    category: 'Sustainability',
    tags: ['organic', 'farming', 'sustainability', 'environment'],
    image: '/categories/pickle.jpg',
    featured: false,
    views: 1456,
    likes: 167
  },
  {
    id: 'cooking-tips',
    title: '10 Expert Tips for Perfect Dosa Making',
    excerpt: 'Master the art of dosa making with these professional tips. From batter consistency to pan temperature, learn the secrets that make restaurant-quality dosas at home.',
    content: 'Comprehensive dosa making guide...',
    author: 'Chef Venkat',
    authorAvatar: '/avatars/author6.jpg',
    publishedAt: '2024-08-10',
    readTime: '6 min read',
    category: 'Cooking Tips',
    tags: ['dosa', 'cooking', 'tips', 'techniques'],
    image: '/products/dosa-batter.jpg',
    featured: false,
    views: 4321,
    likes: 567
  },
  {
    id: 'food-preservation',
    title: 'Ancient Food Preservation Techniques Still Relevant Today',
    excerpt: 'Discover how traditional methods of food preservation like sun-drying, fermentation, and oil preservation remain superior to many modern techniques.',
    content: 'In-depth look at preservation methods...',
    author: 'Lakshmi Prasad',
    authorAvatar: '/avatars/author7.jpg',
    publishedAt: '2024-08-05',
    readTime: '9 min read',
    category: 'Food Science',
    tags: ['preservation', 'traditional', 'techniques', 'storage'],
    image: '/categories/Non-Veg.jpg',
    featured: false,
    views: 1789,
    likes: 203
  },
  {
    id: 'festive-foods',
    title: 'Festive Foods: Celebrations Through South Indian Cuisine',
    excerpt: 'Explore the rich tradition of festive foods in South India. From Onam Sadhya to Pongal celebrations, discover the cultural significance of traditional dishes.',
    content: 'Cultural exploration of festive foods...',
    author: 'Radha Krishnan',
    authorAvatar: '/avatars/author8.jpg',
    publishedAt: '2024-07-30',
    readTime: '11 min read',
    category: 'Culture & Heritage',
    tags: ['festivals', 'culture', 'celebrations', 'traditional'],
    image: '/categories/pickles-banner.jpg',
    featured: false,
    views: 2987,
    likes: 345
  }
];

const categories = ['All', 'Traditional Recipes', 'Food Science', 'Health & Nutrition', 'Culture & Heritage', 'Sustainability', 'Cooking Tips'];

export default function BlogsPage() {
  const [selectedCategory, setSelectedCategory] = useState('All');
  const [searchQuery, setSearchQuery] = useState('');

  const filteredPosts = blogPosts.filter(post => {
    const matchesCategory = selectedCategory === 'All' || post.category === selectedCategory;
    const matchesSearch = post.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
                          post.excerpt.toLowerCase().includes(searchQuery.toLowerCase()) ||
                          post.tags.some(tag => tag.toLowerCase().includes(searchQuery.toLowerCase()));
    return matchesCategory && matchesSearch;
  });

  const featuredPosts = blogPosts.filter(post => post.featured);

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  const formatNumber = (num: number) => {
    if (num >= 1000) {
      return (num / 1000).toFixed(1) + 'k';
    }
    return num.toString();
  };

  return (
    <div className="min-h-screen bg-gray-50 pt-20 pb-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
            üì∞ <span className="text-orange-600">Raaga Food Stories</span>
          </h1>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Discover the rich heritage, science, and artistry behind authentic South Indian cuisine. 
            From traditional recipes to modern nutritional insights.
          </p>
        </div>

        {/* Search Bar */}
        <div className="mb-8 max-w-md mx-auto">
          <div className="relative">
            <input
              type="text"
              placeholder="Search articles..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full px-4 py-3 pl-12 border border-gray-300 rounded-full focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
            />
            <svg 
              className="absolute left-4 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5"
              fill="none" 
              stroke="currentColor" 
              viewBox="0 0 24 24"
            >
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>
        </div>

        {/* Featured Articles */}
        <div className="mb-12">
          <h2 className="text-2xl font-bold text-gray-900 mb-6 flex items-center">
            <span className="mr-2">‚≠ê</span>
            Featured Articles
          </h2>
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {featuredPosts.slice(0, 2).map((post) => (
              <article
                key={post.id}
                className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1"
              >
                <div className="relative h-64 overflow-hidden">
                  <Image
                    src={post.image}
                    alt={post.title}
                    fill
                    className="object-cover hover:scale-105 transition-transform duration-300"
                  />
                  <div className="absolute top-4 left-4">
                    <span className="bg-orange-600 text-white px-3 py-1 rounded-full text-sm font-medium">
                      Featured
                    </span>
                  </div>
                  <div className="absolute inset-0 bg-gradient-to-t from-black/50 to-transparent" />
                </div>
                <div className="p-6">
                  <div className="flex items-center gap-4 mb-3">
                    <span className="bg-orange-100 text-orange-600 px-2 py-1 rounded-full text-xs font-medium">
                      {post.category}
                    </span>
                    <span className="text-sm text-gray-500">{post.readTime}</span>
                    <span className="text-sm text-gray-500">{formatDate(post.publishedAt)}</span>
                  </div>
                  <h3 className="text-xl font-bold text-gray-900 mb-3 line-clamp-2">{post.title}</h3>
                  <p className="text-gray-600 mb-4 line-clamp-3">{post.excerpt}</p>
                  
                  <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center">
                      <div className="w-8 h-8 bg-orange-200 rounded-full flex items-center justify-center mr-3">
                        <span className="text-orange-600 font-semibold text-sm">
                          {post.author.split(' ').map(n => n[0]).join('')}
                        </span>
                      </div>
                      <span className="text-sm font-medium text-gray-700">{post.author}</span>
                    </div>
                    <div className="flex items-center gap-4 text-sm text-gray-500">
                      <span className="flex items-center">
                        <svg className="w-4 h-4 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                        </svg>
                        {formatNumber(post.views)}
                      </span>
                      <span className="flex items-center">
                        <svg className="w-4 h-4 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
                        </svg>
                        {formatNumber(post.likes)}
                      </span>
                    </div>
                  </div>

                  <Link
                    href={`/blogs/${post.id}`}
                    className="inline-flex items-center text-orange-600 font-medium hover:text-orange-700 transition-colors"
                  >
                    Read More
                    <svg className="ml-1 w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                  </Link>
                </div>
              </article>
            ))}
          </div>
        </div>

        {/* Category Filter */}
        <div className="mb-8">
          <h2 className="text-2xl font-bold text-gray-900 mb-6">All Articles</h2>
          <div className="flex flex-wrap gap-2 mb-6">
            {categories.map((category) => (
              <button
                key={category}
                onClick={() => setSelectedCategory(category)}
                className={`px-4 py-2 rounded-full text-sm font-medium transition-colors ${
                  selectedCategory === category
                    ? 'bg-orange-600 text-white'
                    : 'bg-white text-gray-700 hover:bg-orange-50 border border-gray-300'
                }`}
              >
                {category}
              </button>
            ))}
          </div>
        </div>

        {/* Articles Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {filteredPosts.map((post) => (
            <article
              key={post.id}
              className="bg-white rounded-xl shadow-md overflow-hidden hover:shadow-lg transition-all duration-300"
            >
              <div className="relative h-48 overflow-hidden">
                <Image
                  src={post.image}
                  alt={post.title}
                  fill
                  className="object-cover hover:scale-105 transition-transform duration-300"
                />
                {post.featured && (
                  <div className="absolute top-4 left-4">
                    <span className="bg-yellow-500 text-white px-2 py-1 rounded-full text-xs font-bold">
                      ‚≠ê Featured
                    </span>
                  </div>
                )}
              </div>
              
              <div className="p-6">
                <div className="flex items-center gap-3 mb-3">
                  <span className="bg-orange-100 text-orange-600 px-2 py-1 rounded-full text-xs font-medium">
                    {post.category}
                  </span>
                  <span className="text-xs text-gray-500">{post.readTime}</span>
                </div>
                
                <h3 className="text-lg font-bold text-gray-900 mb-2 line-clamp-2">{post.title}</h3>
                <p className="text-gray-600 mb-4 text-sm line-clamp-3">{post.excerpt}</p>
                
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center">
                    <div className="w-6 h-6 bg-orange-200 rounded-full flex items-center justify-center mr-2">
                      <span className="text-orange-600 font-semibold text-xs">
                        {post.author.split(' ').map(n => n[0]).join('')}
                      </span>
                    </div>
                    <span className="text-xs font-medium text-gray-700">{post.author}</span>
                  </div>
                  <span className="text-xs text-gray-500">{formatDate(post.publishedAt)}</span>
                </div>

                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3 text-xs text-gray-500">
                    <span className="flex items-center">
                      <svg className="w-3 h-3 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                      </svg>
                      {formatNumber(post.views)}
                    </span>
                    <span className="flex items-center">
                      <svg className="w-3 h-3 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
                      </svg>
                      {formatNumber(post.likes)}
                    </span>
                  </div>
                  <Link
                    href={`/blogs/${post.id}`}
                    className="text-orange-600 font-medium hover:text-orange-700 transition-colors text-sm"
                  >
                    Read More ‚Üí
                  </Link>
                </div>

                {/* Tags */}
                <div className="mt-4 pt-4 border-t border-gray-100">
                  <div className="flex flex-wrap gap-1">
                    {post.tags.slice(0, 3).map((tag) => (
                      <span 
                        key={tag}
                        className="text-xs bg-gray-100 text-gray-600 px-2 py-1 rounded-full"
                      >
                        #{tag}
                      </span>
                    ))}
                  </div>
                </div>
              </div>
            </article>
          ))}
        </div>

        {/* No Results */}
        {filteredPosts.length === 0 && (
          <div className="text-center py-12">
            <div className="text-gray-400 text-6xl mb-4">üìù</div>
            <h3 className="text-xl font-semibold text-gray-600 mb-2">No articles found</h3>
            <p className="text-gray-500">Try adjusting your search or category filter.</p>
          </div>
        )}

        {/* Newsletter Signup */}
        <div className="mt-16 bg-orange-50 rounded-2xl p-8 text-center">
          <h2 className="text-3xl font-bold text-gray-900 mb-4">
            Stay Updated with Food Stories
          </h2>
          <p className="text-xl text-gray-600 mb-6 max-w-2xl mx-auto">
            Subscribe to our newsletter and get the latest articles about traditional cuisine, health tips, and cooking techniques delivered to your inbox.
          </p>
          <div className="max-w-md mx-auto flex gap-4">
            <input
              type="email"
              placeholder="Enter your email"
              className="flex-1 px-4 py-3 border border-gray-300 rounded-full focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
            />
            <button className="bg-orange-600 text-white px-6 py-3 rounded-full font-semibold hover:bg-orange-700 transition-colors">
              Subscribe
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}